
.logo-container {
  display: -ms-flexbox;
  display: flex;
  
}
.logo-container h1 {
  margin-left: 20px;
}
.logo {
  height: 70px; 
}
.wrapper-navbar{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(75, 72, 72, 0.5);
  padding: 10px 0;
  margin-bottom: 30px;
  color : #fff;
}
.wrapper-navbar a, i {
  cursor: pointer;
  color: #fff;
  font-size: 1.2rem;
}
.myprofile {
  border-radius: 50%;
  width: 10em;
  height: 10em;
  background-size: cover;
}
.logout{
  padding-bottom: 3px;
}
.logout:hover{
  border-bottom: 2px solid #fff;
  -webkit-transition: 0.3s ease-out;
  -o-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}



