@import url('https://fonts.googleapis.com/css?family=Oswald:300,400,700');

html,body {
    font-family: 'Oswald', sans-serif;
    font-weight: 700;
    background-image : url(../img/milky-way-BG.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

html, body {
  margin: 1rem;
}
h1, h2, h3, h4, h5{
  font-family : 'Oswald'
}
.ui.grid{
  margin : 0;
}
.imgContainer {
    width: 20rem;
    height: 30rem;
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-position: top;
    margin: 0.8rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: end;
        justify-content: flex-end;
    font-size: 1rem;
    border: 0.4rem inset #fcc200;
    -webkit-box-shadow: -15px 15px 25px 0px #000;
            box-shadow: -15px 15px 25px 0px #000;
    text-decoration: none;
    -webkit-transition: background-size 10s cubic-bezier(0, 0.76, 0, 1.15);
    -o-transition: background-size 10s cubic-bezier(0, 0.76, 0, 1.15);
    transition: background-size 10s cubic-bezier(0, 0.76, 0, 1.15); 
}

.charList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background: transparent;
}

.character-wrapper {
  background: rgba(75, 72, 72, 0.5);
  margin : 0;
  color: white;
  padding: 0.2rem;
  padding-left: 0.4rem;
  font-weight: 100;
}

.character-wrapper .image {
  width: 300px;
  height: 300px;
  background-repeat: no-repeat;
  margin-top : 1rem;
}

.character-wrapper a {
  color: #fdf253;
  text-decoration: none;
}

.character-wrapper .heart {
  color: #fff;
  font-size: 2rem;

}

.character-wrapper .heart.active {
  color: red;
}

.active {
  color: red;
}

.character-name {
  font-weight: 400;
  font-size: 1.2rem;
}

.character-name, .character-born, .character-location {
  color: #fff;
}

.styles_modal__gNwvD {
  width: 500px;
  height: 300px;
  border: 1px solid #000;
  -webkit-box-shadow: 67px -63px 19px -23px rgba(0,0,0,1);
  box-shadow: 67px -63px 19px -23px rgba(0,0,0,1);
}


