.select-side-wrapper {
    height: 100vh;
    background-color: #000;
}

.flex {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.flex .image-wrapper {
    width: 50%;
}

.image-choice {
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position : top;
       object-position : top;
    width: 100%;
    height: 100%;
    opacity: 0.6;
    position: relative;
    cursor : pointer;
}

.light-side{
    background : -webkit-gradient(linear, left bottom, left top, from(rgba(0, 80, 161, 0.2)), to(#000000));
    background : -webkit-linear-gradient(bottom, rgba(0, 80, 161, 0.2), #000000);
    background : -o-linear-gradient(bottom, rgba(0, 80, 161, 0.2), #000000);
    background : linear-gradient(to top, rgba(0, 80, 161, 0.2), #000000);
}
.light-side-children, .dark-side-children{
    font-family: 'starjedi';
    font-size: 1.5rem;
    color: #000;
}
.light-side-children{
    position: absolute;
    left: 60px;
    top: 140px;
}
.dark-side-children{
    position: absolute;
    right: 60px;
    top: 140px;
}
.light-side:hover .light-side-children{
    color: #fcc200;
    -webkit-transition: 0.5s ease-in;
    -o-transition: 0.5s ease-in;
    transition: 0.5s ease-in;
}
.dark-side:hover .dark-side-children{
    color: #fcc200;
    -webkit-transition: 0.5s ease-in;
    -o-transition: 0.5s ease-in;
    transition: 0.5s ease-in;
}
.dark-side{
    background : -webkit-gradient(linear, left bottom, left top, from(rgba(169, 34, 28, 0.2)), to(#000000));
    background : -webkit-linear-gradient(bottom, rgba(169, 34, 28, 0.2), #000000);
    background : -o-linear-gradient(bottom, rgba(169, 34, 28, 0.2), #000000);
    background : linear-gradient(to top, rgba(169, 34, 28, 0.2), #000000);
}
.light-side:hover{
    background : -webkit-gradient(linear, left bottom, left top, from(rgba(0, 80, 161, 1)), to(#000000));
    background : -webkit-linear-gradient(bottom, rgba(0, 80, 161, 1), #000000);
    background : -o-linear-gradient(bottom, rgba(0, 80, 161, 1), #000000);
    background : linear-gradient(to top, rgba(0, 80, 161, 1), #000000);
}
.dark-side:hover{
    background : -webkit-gradient(linear, left bottom, left top, from(rgba(169, 34, 28, 1)), to(#000000));
    background : -webkit-linear-gradient(bottom, rgba(169, 34, 28, 1), #000000);
    background : -o-linear-gradient(bottom, rgba(169, 34, 28, 1), #000000);
    background : linear-gradient(to top, rgba(169, 34, 28, 1), #000000);
}


.home-title {
    font-size: 45px;
    position: absolute;
    top: 45%;
    text-align: center;
    z-index: 1;
    color: #fcc200;
    font-family: 'starjedi';
}



